  .switch {
    position: relative;
  }
  .switch input {
    height: 100%;
    width: 100%;
    position: absolute;
    left: 0;
    top: 30;
    opacity: 0;
    z-index: 100;
    cursor: pointer;
  }
  .switch label {
    height: 20px;
    width: 50px;
    background-color: #666666;
    border-radius: 100px;
    display: block;
    box-shadow: inset 0 0 20px rgba(0,0,0,0.2), inset 0 0 5px -2px rgba(0,0,0,0.4);
  }
  .switch label .bulb {
    height: 12px;
    width: 20px;
    border-radius: 50%;
    position: relative;
    top: 10px;
    left: 0px;
    display: block;
    transition: 0.7s;
  }
  .switch label .bulb .bulb-center {
    position: absolute;
    display: block;
    height: 20px;
    width: 21px;
    background-color: #CDBA6D;
    border-radius: 50%;
    top: 50%;
    left: 50%;
    transition: 0.7s;
    transform: translate(-50%, -80%);
    box-shadow: inset 0 0 0 4px #CDBA6D;
  }

  .switch input:checked ~ label .bulb {
    left: 25px;
    background-color: #a7694a;
  }
  .switch input:checked ~ label .bulb > .bulb-center {
    background-color: #feed6b;
    box-shadow: inset 0 0 0 2px #fdec6a, 0 0 6px 5px #bca83c, 0 0 2px 4px #a1664a;
  }
 
